@import '~antd/dist/antd.css';

.ant-layout {
  height: 100vh;
  overflow: auto;
}

.ant-layout-header {
  color: #fff;
  font-size:32px;
  text-align:center;
  display: inline-block;
}

.ant-layout-footer {
  color: #fff;
  background:black;
}

.ant-layout-content {
  color: #fff;
  /* margin: 24px 16px 0px; */
  margin: 0 16px;
}

.ant-select-selector {
  border-color: lightslategrey;
}

.ant-select-selection-item {
  text-align: left;
}

/*text list in tree select*/
.ant-select-tree-title {
  text-align: left;
}
